.message {
    padding-right: 70px;
    display: flex;
    /*justify-content: center;*/
    flex-direction: row-reverse;
}
.imageAndText {
    display: flex;
    align-items: center;
    gap:25px;
    flex-direction: row-reverse;
}

.imageAndText img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.text {
    background-color: #0066CC;
    display: block;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 20px;
    max-width: 575px;
    word-wrap: break-word;
    overflow-wrap: break-word;
    box-sizing: border-box;
}

.text:after {

}

.name {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 16px;
    color: white;
}


.messageText {
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    font-size: 16px;
    color: white;
    word-wrap: break-word;
    overflow-wrap: break-word;
}

.time {
    padding-left: 13px;
    font-weight: 600;
    font-size: 10px;
    color: black;
}