.friendMessage {
    padding: 0 70px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.friendImageAndText {
    display: flex;
    align-items: center;
    gap:25px
}

.friendImageAndText img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.friendText {
    background-color: #F5F7FB;
    display: inline-block;
    width:auto;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 20px;
}

.friendText:after {

}

.friendName {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 16px;
    color: black;
}


.friendMessageText {
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    font-size: 16px;
    color: black;
    max-width: 575px; /* Ограничивает ширину блока */
    word-wrap: break-word; /* Перенос слов */
    overflow-wrap: break-word; /* Альтернативное свойство для переноса */
}



.friendTime {
    padding-left: 13px;
    font-weight: 600;
    font-size: 10px;
    color: black;
}
